/* Import Google Font */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

body {
  background-color: #2c2543;
  color: white;
  overflow: hidden; /* Prevent scrolling */
}

.page-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #372f56; /* Darker purple background */
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1); /* Subtle shadow */
}

.page-content {
  padding: 20px;
}

.page-title {
  font-size: 28px;
  color: #f9c82d; /* Yellow text color */
  margin-bottom: 10px;
  text-align: center;
}

.page-description {
  font-size: 18px;
  color: #ccc; /* Light gray text color */
  margin-bottom: 20px;
  text-align: center;
}

.task-list {
  margin-top: 20px;
}

.task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #544c75; /* Darker purple border */
  padding: 15px 0;
  transition: background-color 0.3s ease; /* Smooth transition */
}

.task-item:hover {
  background-color: #544c75; /* Darker purple background on hover */
}

.task-info {
  flex: 1;
}

.task-text {
  font-size: 20px;
  color: #f9c82d; /* Yellow text color */
  margin-bottom: 5px;
}

.task-value {
  font-size: 16px;
  color: #ccc; /* Light gray text color */
}

.go-button {
  background-color: #f9c82d; /* Yellow button */
  color: #372f56; /* Dark purple text color */
  padding: 10px 20px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease; /* Smooth transitions */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Subtle shadow */
}

.go-button:hover {
  background-color: #f2b705; /* Lighter yellow on hover */
}

.go-button[disabled] {
  background-color: #888; /* Grayed-out button */
  cursor: not-allowed;
}

.action-button {
  font-size: 16px;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.1s ease-in-out; /* Smooth transitions */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Subtle shadow */
}

.action-button:hover {
  background-color: #544c75; /* Darker purple background on hover */
}

/* Responsive Design */
@media (max-width: 600px) {
  .page-title {
    font-size: 24px; /* Adjust font size for smaller screens */
  }

  .page-description {
    font-size: 16px; /* Adjust font size for smaller screens */
  }

  .task-text {
    font-size: 18px; /* Adjust font size for smaller screens */
  }

  .task-value {
    font-size: 14px; /* Adjust font size for smaller screens */
  }

  .go-button,
  .action-button {
    font-size: 14px; /* Adjust button font size for smaller screens */
    padding: 8px 16px; /* Adjust button padding for smaller screens */
  }
}
