:root {
  --main-bg-color: #87ceeb;     /* Light blue background */
  --game-over-bg-color: #ff6347; /* Tomato red background for game over screen */
  
}

.Pigeon {
  position: relative;
  width: 100%;
  max-width: 600px;
  height: 100vh;
  border: 1px solid #000;
  overflow: hidden;
  background: var(--main-bg-color) url('assets/p1.jpg') no-repeat center center;
  background-size: cover;
  transition: background-color 0.5s ease;
  margin: 0 auto;
  box-sizing: border-box;
  padding-bottom: 56.25%;
  position: relative; /* Ensure the pseudo-element is positioned correctly */
}

.Pigeon::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3); /* Semi-transparent black overlay */
}






@media (max-width: 600px) {
  .App {
    padding-bottom: 100%; /* Adjust aspect ratio for smaller screens */
  }
}


.game-over {
  background-color: var(--game-over-bg-color);  /* Game over screen background color */
  position: relative;
  width: 100%; /* Full width of its container */
  max-width: 600px; /* Maximum width to control layout on larger screens */
  height: 100vh; /* Full height of the viewport */
  border: 1px solid #000;
  overflow: hidden;
  
  transition: background-color 0.5s ease;
  margin: 0 auto; /* Center align horizontally */
  box-sizing: border-box; /* Include border in width calculation */
  padding-bottom: 56.25%; /* Maintain a 16:9 aspect ratio for responsive layout */
}

.game-over-message {
  z-index: 2;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 40px;
  font-weight: bold;
  color: #f3f0f1;  /* Vibrant pink color for game over message */
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5); /* Soft black shadow */
  letter-spacing: 2px; /* Spacing between letters */
  text-transform: uppercase; /* Uppercase letters */
  padding: 10px 20px; /* Padding around the text */
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent white background */
  border-radius: 10px; /* Rounded corners */
}



.bird {
  position: absolute;
  width: 50px;
  height: 50px;
  user-select: none;
}

.obstacle {
  position: absolute;
  border-radius: 10%; /* Rounded edges for obstacles */
  user-select: none;
}

.score {
  z-index: 2;
  position: absolute;
  top: 10px;
  left: 10px;
  font-family: 'Roboto', sans-serif; /* Modern sans-serif font */
  font-size: 18px; /* Adjust font size for smaller screens */
  font-weight: bold;
  color: #4CAF50; /* Green color for score */
  background-color: #FFFFFF; /* White background */
  padding: 8px 14px; /* Adjust padding for smaller screens */
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Soft shadow for depth */
}

.high-score {
  z-index: 2;
  position: absolute;
  top: 10px;
  right: 10px;
  font-family: 'Roboto', sans-serif; /* Modern sans-serif font */
  font-size: 18px; /* Adjust font size for smaller screens */
  font-weight: bold;
  color: #FF9800; /* Orange color for high score */
  background-color: #FFFFFF; /* White background */
  padding: 8px 14px; /* Adjust padding for smaller screens */
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Soft shadow for depth */
}

/* Media Query for Small Screens */
@media screen and (max-width: 600px) {
  .score, .high-score {
    font-size: 16px; /* Further reduce font size for very small screens */
    padding: 6px 10px; /* Adjust padding for very small screens */
  }
}

