/* src/UserProfile.css */
.user-profile {
  text-align: center;
  padding: 20px;
}

.user-details {
  margin-bottom: 20px;
}

.user-icon {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.back-button {
  position: absolute;
  top: 10px;
  left: 10px;
}

.restart-button,
.give-life-button {
  background-color: #4caf50; /* Green background */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  margin: 10px;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease, transform 0.2s ease;
  justify-content: center;
  align-items: center;
}

.restart-button:hover,
.give-life-button:hover {
  background-color: #45a049; /* Darker green on hover */
  transform: scale(1.05); /* Slightly increase size on hover */
}

.restart-button:active,
.give-life-button:active {
  transform: scale(0.95); /* Scale down briefly when clicked */
  box-shadow: none; /* Remove shadow when clicked */
}

.high-score-message {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.9); /* Semi-transparent background */
  padding: 20px 40px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add a shadow for some depth */
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  color: #4CAF50; /* Green color for the text */
  z-index: 1000; /* Ensure it stays above other elements */
}
