

/* Import Google Font */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

body {
  background-color: #2c2543;
  color: white;
 
  overflow: hidden; /* Prevent scrolling */
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  min-height: 90vh; /* Adjust to fill viewport height */
  padding: 20px;
}

.header {
  text-align: center;
  margin-bottom: 20px;
}

.userInfo {
  text-align: center;
  margin-top: 10px;
}

.coinCount {
  display: flex;
  align-items: center;
  justify-content: center;
}

.username {
  font-size: 24px; /* Adjust font size to fit within the container */
  font-weight: 700;
}

.coinValue {
  margin: 10px;
  font-size: 40px; /* Adjust font size to fit within the container */
  font-weight: 700;
}

.coinIcon {
  width: 40px; /* Adjust size to fit within the container */
  height: 40px;
  margin: 8; /* Adjust size to fit within the container */
  border-radius: 50%;
  background-color: #4d4278;
  background-image: url('assets/famtap.jpg');
  background-size: cover;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  transition: transform 0.1s ease-in-out;
}

.legendaryStatus {
  font-size: 15px;
  margin-top: 5px;
}

@keyframes fadeInOut {
  0% { opacity: 0; }
  50% { opacity: 1; }
  100% { opacity: 0; }
}

.coinContainer {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0; /* Adjust margin to fit content */
  position: relative;
}

.coin {
  width: 250px; /* Adjust size to fit within the container */
  height: 250px; /* Adjust size to fit within the container */
  border-radius: 50%;
  background-color: #4d4278;
  background-image: url('assets/famtap.jpg');
  background-size: cover;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  transition: transform 0.1s ease-in-out;
}

.coin.tapped {
  transform: scale(0.98);
}

.plusOne {
  position: absolute;
  color: white;
  font-size: 20px; /* Adjust font size to fit within the container */
  font-weight: bold;
  animation: moveUp 1s ease-out forwards;
}

@keyframes moveUp {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  50% {
    opacity: 1;
    transform: translateY(-25px); /* Adjust translate value */
  }
  100% {
    opacity: 0;
    transform: translateY(-50px); /* Adjust translate value */
  }
}

.energyContainer {
  width: 100%; /* Adjust width to fit within the container */
  text-align: center;
}

.energyTitle {
  font-size: 14px;
  margin-bottom: 5px;
}

.energyBar {
  width: 100%;
  height: 8px; /* Adjust height to fit within the container */
  background-color: #555555;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 5px;
}

.energyFill {
  height: 100%;
  background-color: #F9C82D;
}

.energyText {
  font-size: 12px;
}

.bottomNav {
  width: 100%;
  display: flex;
  justify-content: space-around;
  background-color: #4d4278;
  padding: 8px 0;
  position: fixed;
  bottom: 0;
}

.actionButton {
  background-color: #4d4278;
  color: white;
  padding: 8px 16px; /* Adjust padding to fit within the container */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px; /* Adjust font size to fit within the container */
  font-weight: 700;
  transition: background-color 0.3s ease, transform 0.1s ease-in-out;
}

.actionButton:hover {
  background-color: #5e549b;
}

.actionButton:active {
  transform: scale(0.95);
}

.celebration {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);


background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 10px;
  z-index: 9999;
  animation: funnyCelebration 1s ease-in-out;
}

@keyframes funnyCelebration {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

.id {
    padding: 20px;
    text-align: center;
}

/* Name and High Score styles */
.name-highscore p {
    font-size: 18px;
    margin: 10px 0;
}

.name-highscore strong {
    font-weight: bold;
}


.fakemessage {
  font-weight: bold;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: red;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 10px;
  z-index: 9999;
  animation: funnyCelebration 1s ease-in-out;
  text-decoration: line-through; /* Adding strike-through */
  animation: rainbowText 2s infinite; /* Adding rainbow color animation */
}

@keyframes rainbowText {
  0% {
    color: red;
  }
  25% {
    color: orange;
  }
  50% {
    color: yellow;
  }
  75% {
    color: green;
  }
  100% {
    color: blue;
  }
}

@keyframes funnyCelebration {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  25% {
    transform: translate(-50%, -50%) rotate(5deg);
  }
  50% {
    transform: translate(-50%, -50%) rotate(-5deg);
  }
  75% {
    transform: translate(-50%, -50%) rotate(3deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
}
